// colors
$primary_color: blue;
$primary_text: white;
$accent:orange;
$main:white;
$main_header:rgba(0, 0, 0, 0.78);
$main_subHeader:rgba(0, 0, 0, 0.68);
$main_text:rgba(0, 0, 0, 0.575);
$accent_text:white;
$error:red;

//sizes
$max_wrapper:600px;
$reg_padding:20px;

body,
html {
  color: $main_text;
  font-size: 15px;
}

h1 {
  color: main_header;
}

.message {
  max-width: 500px;
  padding: $reg_padding;
  background-color: $main;

  &--error {
    background-color: $error;
    color: white;
  }
}

button {
  width: fit-content;
  min-width: 100px;
  padding: 10px;
  border-radius: 40px;
  border: 1px solid;
  background: none;
  text-transform: uppercase;
}

.form {
  &__inputs {
    &--flex {
      display: grid;
      gap: 5px;
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

